/*
  The whole installment methods is a third-party widgets,
  So there aren't a custom classes (as other components) to style them.
*/

salla-installment:empty {
  display: none;
}
/*
* Installment::Tabby
*/
#tabbyPromoWrapper {
  background: white;
  border-radius: 0.375rem;
  transition: box-shadow 0.5s cubic-bezier(0.4, 0, 0.2, 1);
  margin-bottom: 20px;

  .salla-y &{
    border: 1px solid var(--color-grey-dark);
    border-radius: 12px
  }

  &:hover{
    box-shadow: 0 0 #0000, 0 0 #0000, 5px 10px 30px #2B2D340D;
  }

  #tabbyPromo {
    * {
      font-family: var(--font-main);
    }

    > div > div {
      max-width: none;
      //padding: 15px;
      box-shadow: none;
      border: none;
    }

    .tabby-promo-snippet {
      max-width: 100%;
      min-height: 100px;
      padding: 18px 20px;
      // border-color: var(--color-grey-dark);
      border: none !important;


      &__text, &__link {
        font-size: var(--font-sm);
        color: var(--color-text) !important;
      }

      &__link {
        font-weight: bold;
      }
    }
  }
}

.tabby-promo-wrapper {
  #tabby-promo {
    font-family: var(--font-main) !important;

    .tabby-promo {
      &__feature-title {
        font-size: var(--font-md);
      }

      &__feature-desc {
        font-size: var(--font-sm);
        line-height: 20px;
      }
    }
  }
}

/*
* Installment::Tamara & Spotii
*/
.tamara-product-widget{
  margin-bottom: 20px;
}

.tamara-product-widget,
.spotii-wrapper {
  min-height: 100px;
  position: relative;
  color: var(--color-text);
  font-size: var(--font-sm);
  line-height: 1.25;
  padding: 18px 20px 18px 100px !important;
  background: white;
  border-radius: 0.375rem;
  transition: box-shadow 0.5s cubic-bezier(0.4, 0, 0.2, 1);

  .salla-y &{
    border-radius: 12px;
    border: 1px solid var(--color-grey-dark);
  }

  &:hover{
    box-shadow: 0 0 #0000, 0 0 #0000, 5px 10px 30px #2B2D340D;
  }
  .spotii-logo {
    float: left;
    margin: 0 0 0 -75px;
  }
}
.ltr .tamara-product-widget,
.ltr .spotii-wrapper{
  text-align: left;
  padding: 18px 100px 18px 20px !important;
  .spotii-logo {
    float: right;
    margin: 0 -75px 0 0;
  }
  .spotii-product-widget {
    text-align: left !important;
  }
}

.spotii-wrapper {
  margin-bottom: 20px;
  .spotii-promo {
    font-size: var(--font-md);
  }

  .spotii-product-widget {
    font-size: var(--font-sm) !important;
    margin-top: 10px;
  }
}

.tamara-product-widget{
  .tamara-logo {
    position: absolute;
    left: 20px;
    top: 18px;
    margin: 0 !important;

    .ltr &{
      right: 20px;
      left: auto;
    }
  }

  span {
    font-family: var(--font-main);
    font-size: var(--font-sm);
    color: var(--color-text);

    &:last-child {
      display: block;
      position: relative;
      margin-top: 8px;
    }
  }
}

.tamara-popup {
  &__wrap {
    overflow: auto !important;
  }
}
